
import bridge from "@vkontakte/vk-bridge";

// –æ—Ç–∫—Ä—ã—Ç–∏–µ —Ä–µ–∫–ª–∞–º–Ω–æ–≥–æ —Ä–æ–ª–∏–∫–∞
 export function nativeAds() {
  return bridge
    .send("VKWebAppShowNativeAds", {
      ad_format: "preloader",
    })
    .then((res) => console.log("VKWebAppShowNativeAds RES", res))
    .catch((err) => console.log("VKWebAppShowNativeAds ERR", err));
}

/**
 * –û—Ç–∫—Ä—ã–≤–∞–µ—Ç —Ä–µ–∫–ª–∞–º–Ω—ã–π –±–∞–Ω–Ω–µ—Ä –Ω–∞ –≤–µ—Å—å —ç–∫—Ä–∞–Ω
 * @returns {Promise<boolean>}
 */
export async function showNativeAd() {
  try {
    const data = await bridge.send("VKWebAppShowNativeAds", {
      ad_format: "preloader",
    });

    return data.result !== undefined ? data.result : false;
  } catch (e) {
    return false;
  }
}